#home {
  position: relative;
  background: url('../../assets/cover-2.webp');

  background-size: cover;
  background-repeat: no-repeat;
  height: 100vh;
  width: 100%;

  .app__wrapper {
    padding: 0;

    .copyright {
      display: none;
    }
  }
}

.app__header {
  flex: 1;
  width: 100%;
  height: 90%;
  flex-direction: row;

  padding: 6rem 2rem;

  @media screen and (min-width: 2000px) {
    padding-top: 8rem;
  }

  @media screen and (max-width: 1200px) {
    flex-direction: column;
  }

  @media screen and (max-width: 450px) {
    padding: 6rem 1rem 2rem;
  }
}

.app__header-info {
  flex: 0.65;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;

  margin: 0 2rem;

  @media screen and (max-width: 1200px) {
    width: 100%;
    margin-right: 0rem;
  }

  // @media screen and (max-width: 450px) {
  //   width: 100px;
  //   height: 30px;
  // }
}

.app__header-extInfo {
  position: relative;
  top: 50;
  left: 0;
}

.app__header-badge {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  flex-direction: column;


  .badge-cmp,
  .tag-cmp {
    padding: 1rem 2rem;
    background: var(--white-color);
    border-radius: 15px;
    flex-direction: row;
    width: 400px;

    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);

    @media screen and (max-width: 450px) {
      width: auto;
      margin: 1rem;
      margin-left: -1rem;
    }
  }

  .tag-cmp {
    flex-direction: column;
    margin-top: 3rem;

    p {
      width: 100%;
      text-transform: uppercase;
      text-align: right;
    }
  }

  span {
    font-size: 2.5rem;

    @media screen and (min-width: 2000px) {
      font-size: 5rem;
    }
  }

  @media screen and (max-width: 1200px) {
    justify-content: flex-start;
    align-items: flex-start;
  }
}

.app__header-circles {
  flex: 0.75;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: flex-start;
  height: 100%;
  margin-left: 1rem;

  div {
    width: 100px;
    height: 100px;
    border-radius: 50%;

    background: var(--white-color);
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);

    img {
      width: 60%;
      height: 60%;
    }
  }

  // circles with different height & width

  div:nth-child(1) {
    width: 100px;
    height: 100px;
  }

  div:nth-child(2) {
    margin: 1.75rem;
    width: 150px;
    height: 150px;
  }

  div:nth-child(3) {
    width: 70px;
    height: 70px;
  }

  @media screen and (min-width: 2000px) {
    div:nth-child(2) {
      width: 400px;
      height: 400px;
    }

    div:nth-child(3) {
      width: 170px;
      height: 170px;
    }

    div:nth-child(1) {
      width: 200px;
      height: 200px;
    }
  }

  @media screen and (max-width: 1200px) {
    width: 100%;
    flex-direction: row;
    flex-wrap: wrap;
    margin-left: 0;

    div {
      margin: 1rem;
    }
  }
}

.app__header-img {
  flex: 1;
  height: 100%;

  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;

  img {
    width: 50%;
    object-fit: cover;
    z-index: 1;
  }

  .overlay_circle {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 0;
    width: 100%;
    height: 90%;
  }

  @media screen and (max-width: 1200px) {
    margin: 2rem 0;
  }
}

.side__panel {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 20%;
  height: 100%;
  background-color: rgba(52, 58, 64, 0.9);
  z-index: 30;

  .panel-image {
    margin-top: 20%;
    border-radius: 50%;
    height: 160px;
    width: 160px;
    padding: 5px;
    background-color: var(--gray-2);

    img {
      height: 150px;
      width: 150px;
    }
  }

  @media screen and (max-width: 1350px) {
    display: none;
  }
}

.right-panel {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 80%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 30;

  .type-animation {
    margin: 1rem;
    color: var(--white-color);
    font-family: inherit;
    font-size: 1.75rem;
    text-align: center;
  }

  .arrow-animation {
    position: relative;
    top: 10%;
    margin-top: 1rem;
  }

  @media screen and (max-width: 1350px) {
    width: 100%;
  }

  @media screen and (max-width: 650px) {
    .type-animation{
      height: 100px;
      font-size: 1.5rem;
    }
    .arrow-animation {
      position: relative;
      top: 10%;
    }
  }
}